body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei',
    'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

.logo {
  font-size: 17px;
  color: #FFFFFF;
  background: #1A535C;
}

.sergate {
  margin: 16px 0;
}

.sergate .ant-row {
  position: relative;
  flex-wrap: nowrap;
}
.sergate .sg-progress .ant-progress-outer {
  margin-right: calc(-3em - 8px);
  padding-right: calc(3em + 8px);
}

.sergate .sg-progress .ant-progress-text {
	display: inline-block;
	width: 2em;
	margin-left: 8px;
	color: #000000;
    -webkit-text-fill-color: #000000;
	font-size: 1em;
	line-height: 1;
	text-align: left;
	vertical-align: middle;
	word-break: normal;
    opacity: 1!important;
}

.ant-layout .ant-layout-header {
	background: #1A535C;
}

.sergate .sg-progress .ant-progress-bg {
    background: #1a535c;
}

.sr-head {
  padding-top: 8px;
  padding-bottom: 8px;
}

.sr-head .ant-col {
  text-align: center;
}

.sr-body .ant-col {
  text-align: center;
  padding-top: 8px;
  padding-bottom: 8px;
  border-top: 1px solid #ffffff;
}

.col-num {
  position: absolute;
  width: 16px;
  line-height: 40px;
  left: -16px;
  top: 0;
  text-align: right;
}

.sergate .lastUpdated {
  margin-top: 16px;
  text-align: center;
  background: #FFE66D;
}

.sergate .anticon-check-circle {
  color: #1A535C;
}

.sergate .anticon-warning {
  color: #FF6B6B;
}

.ant-layout .footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
}

body {
  padding-bottom: 69px;
}

@media (max-width: 768px) {
  .logo {
    text-align: center;
  }
  .sr-head {
    font-size: 0.8rem;
  }
  .sr-body .ant-col {
    font-size: 0.8rem;
  }
  .sergate .ant-tag {
    margin-right: 0;
  }
}

@media (max-width: 900px) {
  .sergate .ant-col .ant-progress-outer {
    display: none;
  }
  .sergate .ant-col .ant-progress-text {
    margin-left: 0;
    color: #FFFFFF;
  }

  .sr-head {
    font-size: 10px;
  }
}
